const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)src\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(2);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)src\/(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      './' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)src(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(3);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)src(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      '../' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^(?:\.\.(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)stories(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(3);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?:(?:^|\/|(?:(?:(?!(?:^|\/)\.).)*?)\/)stories(?:\/(?!\.)(?:(?:(?!(?:^|\/)\.).)*?)\/|\/|$)(?!\.)(?=.)[^/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      '../' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}